<template>
  <div id="WriteForm">
      <h3>글쓰기 페이지 입니다.</h3>
        <textarea cols="30" rows="10" v-model="inputTxt"></textarea>
        <button v-on:click="addTodo">글입력</button>
  </div>
</template>

<script>
export default {
  name: 'WriteForm',
  data() {
    return {
      inputTxt: '',
      loading: false
      }
  },
  methods: {
    addTodo () {
        if (this.inputTxt !== '') {
            const value = this.inputTxt && this.inputTxt.trim()
            this.$emit('addTodo', value)
            this.clearInput()
        }
      },
    clearInput () {
      this.newTodoItem = ''
    }
  }
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



