<template>
  <div id="aboutUser">
    <h3>aboutUser</h3>
    <h3 v-for="user in users" v-bind:key="user.id">
        {{user.name}} : {{user.phone}}<!--<router-link :to="{name: 'DetailUser', params: {id: index}}"> </router-link> -->
    </h3>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'aboutUser',
  data() {
    return {
      users:[]
    }
  },
  created () {
    this.$axios.get('/api/users')
    .then((resp) => {
      this.users = resp.data.response
    })
  }
  /** 
  created(){
      this.$http.get('/api/users').then((resp) => {
          this.users = resp.data.response
      })
  }*/
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



